@import './lib/slider.scss';
.main {
    z-index: 9;
}
.mainTitle {
    min-width: span(12);
    margin: 3rem auto 0;
    text-align: center;
    @include breakpoint($sm-less) {
        margin-top: 6rem;
    }
}
.main p {
    // font-size: font-emphasize: 
}
$slides: ('hike', auto, 0), ('team', 0, 0), ('business', 0, calc(50% - 222px)), ('quest', 0, span(2)), ('registration', 0, 0);
@each $name, $textY, $textX in $slides {
    .slide__description_#{$name} {
        bottom: #{$textY};
        right: #{$textX};
    }
    .imageContainer {
        &__image_#{$name} {
            background-image: url('./../img/slides/#{$name}.jpg');
        }
    }
}
.mainSlider {
    max-width: em(1350);
    margin: 0 auto;

    & .owl-stage-outer, & .owl-stage {
        overflow: hidden;
        max-height: em(764);
    }
    @include breakpoint($sm-less) {
        margin-top: 0;
    }
    @include breakpoint($xxs) {
        margin-bottom: 3rem;
        margin-top: 10rem;
    }
}

.programm {
    flex-direction: column;
    max-width: em(1190);
    @include breakpoint($sm-less) {
        max-width: 100%;
    }
    &__title {
        white-space: nowrap;
    }
}
.chain {
    flex-wrap: nowrap;
    &:not(:first-child) {
        margin: 3rem 0 1rem;
    }
    &:nth-child(2n+0) {
        align-self: flex-end;
        & .chainImage {
            order: 1;
        }
        & .chain__paragraph {
            order: 2;
            padding-left: em(50);
            @include breakpoint($sm-less) {
                padding-left: 0;
            }
        }
    }
    &:nth-child(2n+1) {
        align-self: flex-start;
        & .chainImage {
            order: 2;
            @include breakpoint($sm-less) {
                order: 1;
            }
        }
        & .chain__paragraph {
            order: 1;
            padding-right: em(50);
            @include breakpoint($sm-less) {
                order: 2;
                padding-right: 0;
            }
        }
    }
    &:not(:last-of-type) {
        & .chainImage {
            &::after {
                content: "";
                z-index: -1;
                position: absolute;
                top: 100%;
                width: em(13);
                background-color: $gray;
                height: em(75);
                left: em(98.5);
                left: calc(50% - 6.5px);
                @include breakpoint($sm-less) {
                    content: none;
                }
            }
        }
    }
    @include breakpoint($sm-less) {
        align-self: auto $i;
        flex-direction: column;
        max-width: 100%;
        padding: 0 1em;
        align-items: center;
        margin-top: em(65);
    }
    &__paragraph {
        min-width: em(420);
        max-width: em(420);
        @include breakpoint($sm-less) {
            min-width: span(12);
            margin-top: em(35, 21);
        }
        @include breakpoint($sm-less) {
            margin-top: em(26, 21);
        }
    }
}
.items {
    margin-top: 3em;
    max-width: em(1350);

    @supports (display: grid) {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
        grid-gap: 2.5rem;

        // grid-template-rows: 
        // grid-template-areas: "first second third" "fourth fourth fourth";
    }

    @include breakpoint($xxs) {
        justify-content: center;
    }
    @include breakpoint($sm-less) {
        padding: 0 1.5rem;
    }
}

.item {
    margin-left: auto;
    margin-right: auto;
    &:nth-of-type(1) {
        // grid-area: first;
    }
    &:nth-of-type(2) {
        // grid-area: second;
    }
    &:nth-of-type(3) {
        // grid-area: third;
        
    }
    &:nth-of-type(4) {
        @include breakpoint(769px  1024px) {
            grid-area: 2   / 2 / 2 / 2;
        }
    }
}
.item:nth-last-child(2):nth-child(4n) {
    @include breakpoint($sm-less) {
        min-width: span(6);
    }
}
.chainImage {
    position: relative;
    &, &__image {
        width: em(210);
        height: em(210);
        @include breakpoint($sm-less) {
            margin-bottom: 1.5rem;
        }
    }
    &__image {
        @include round(50%);
    }
}
#sliderText {
	will-change: opacity;
}
.sliderText {
    transition: all .4s cubic-bezier(0.2, 0.0, 0.6, 1);
    max-width: 30em;
    bottom: 10em;
    font-size: 16px;
    margin-left: auto;
    right: 10%;
    background-color: $red;
    border-radius: 2px;
    padding: 1.5em 1em 1.75em 1.5em;
    text-align: left;
    color: $white;
    position: absolute;

    @include breakpoint($xs) {
        right: 50%;
        transform: translateX(50%);
        bottom: 0;
    }

    @include breakpoint($xxs) {
    	transform: none;
    	right: 0;
        position: static;
        min-width: 100%;
        margin-top: -4em;
    }
}
.item {
    cursor: pointer;
    min-width: em(250);
    flex-flow: column nowrap;
    display: flex;
    
    @include breakpoint($sm-less) {
        min-width: 50%;
    }
    @include breakpoint($xxs) {
        min-width: span(11);
    }
    
    position: relative;
    transition: .3s ease;
     
    
    &_upper {
        top: -2em;
    }
    &:hover {
        // box-shadow: 0 5px 15px -1px $shadow; // transform: scale(1.02);
    }
    &__title {
        text-align: center;
    }
    &__image {
        margin-top: 1em; // padding: 0 .5em;
    }
    &Description {
        max-width: em(370, 21);
        min-width: span(12);
        &__description {
            &::-webkit-scrollbar-track {
                -webkit-box-shadow: inset 0 0 6px $shadow;
                background-color: $darkGray;
            }
            &::-webkit-scrollbar {
                width: 6px;
                background-color: $darkGray;
            }
            &::-webkit-scrollbar-thumb {
                background-color: $white;
            }
            max-height: em(200, 21);
            overflow-y: auto;
            white-space: pre-line;
            &:first-child {
                margin-top: 1em;
            }
        }
    }
}
.choices {
    min-width: em(265);
    text-align: center;
    justify-content: space-between;
    position: absolute;
    margin-bottom: 1em;
    bottom: 100%;
    left: 10%;
    @include breakpoint($sm-less) {
        top: 100%;
        bottom: auto;
        margin-top: 1em;
    }
    @include breakpoint($xs) {
        left: auto;
        margin: .2rem auto;
    }
}
.button {
    padding: 1em 0;
    color: $white;
    @include round(em(2));
    &_purple {
        background-color: $purple;
    }
    &_red {
        background-color: $red;
    }
}
.choice {
    // max-width: em(125);
    &__button {}
}
.main__registrationButton {
    margin-top: 3em;
    
}
.questions, .catalogs {
    margin: em(100) auto 0;
}
.questions, .catalogs__title {
    @include breakpoint($xxs) {
        padding: 0 5%;
    }
}
.questions {
    max-width: em(605);
}
.catalogs {
    max-width: em(690);
    margin-bottom: em(160);
    &__title {
        margin-bottom: em(21, 24);
        @include breakpoint($xxs) {}
    }
}
.catalogsList {
    @include round(em(4));
    position: relative; // margin: 0 -1.5em;
    .owl-nav {
        position: absolute;
        top: em(-55);
        margin-top: 0;
        right: 0;
        @include breakpoint($xxs) {
            right: 1.5em;
        }
    }
    .owl-prev, .owl-next {
        width: em(55, 14);
    }
}
