.question {
    margin-top: em(60);
}
.registration {
    & .child {
        flex-basis: 50%;
    }
    justify-content: center;
    margin-bottom: em(105);
    @include breakpoint ($xs) {
        padding: 0 5%;
    }
    & .mainTitle {
