        }
    }
}
.registrationIntro {
    @include breakpoint(581px 768px) {
        padding-left: 2.5em;
    }
}
.registrationIntro__paragraph {

}
.main p a {
    color: $darkGray;
    background-image: linear-gradient(to right, $darkGray 0%, $darkGray 100%);
}
.registrationIntro {
    max-width: 30rem;
    margin-top: 1.5em;
    @include breakpoint($xxs) {
        order: 2;
    }
}
.intl-tel-input {
    min-width: 100%;
}
.registrationForm {
    max-width: em(295);
    margin-right: em(50);
    margin-bottom: em(50);
    @include breakpoint ($xs) {
        margin-right: 0;
    }
    @include breakpoint($xxs) {
        order: 3;
        max-width: none;
    }
    &__button {
        margin-top: em(24);
    }
    &__controller {
        &_num_home {
            margin-right: 15%;
        }
        &_num_home, &_num_apartment {
            display: inline-flex;
            max-width: span(5);
        }
    }
}
.iti-flag {
    background-image: url("./../img/flags.png");
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2 / 1), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
    .iti-flag {
        background-image: url("./../img/flags@2x.png");
    }
}
.videos {
    max-width: em(1150);
    margin: 3rem auto 6rem;
    &__title {
        min-width: span(12);
        margin-bottom: 1em; // padding-left: .55em;
    }
    justify-content: center;
    @include breakpoint($xs) {
        padding: 0 1.5rem;
    }
}
.video {
    @extend %flyShadow;
    margin: 0 1rem 1.5em;
    flex: 1 0 25%;
    min-width: 30%;
    padding-bottom: 22%;
    
    max-width: span(8);
    @include round(em(5));
    @include breakpoint($sm-less) {
        padding-bottom: 33.25%;
        min-width: 50%;
    }
    @include breakpoint($xs) {
        min-width: 100%;
        padding-bottom: 56.25%;
    }
}
.video:nth-last-child(2):nth-child(4n) {
    min-width: span(5);
}
.youtube {
    position: relative; // padding-top: 56.25%;
    overflow: hidden;
    cursor: pointer;
}
.youtube img {
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0.7;
    height: 100%;
}
.youtube .play-button {
    width: 90px;
    height: 60px;
    background-color: #333;
    box-shadow: 0 0 30px rgba( 0, 0, 0, 0.6);
    z-index: 1;
    opacity: 0.8;
    border-radius: 6px;
}
.youtube .play-button:before {
    content: "";
    border-style: solid;
    border-width: 15px 0 15px 26.0px;
    border-color: transparent transparent transparent #fff;
}
.youtube img, .youtube .play-button {
    cursor: pointer;
}
.youtube img, .youtube iframe, .youtube .play-button, .youtube .play-button:before {
    position: absolute;
}
.youtube .play-button, .youtube .play-button:before {
    top: 50%;
    left: 50%;
    transform: translate3d( -50%, -50%, 0);
}
.youtube iframe {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.country-list {
    max-width: em(410, 21);
    font-size: em(21);
    color: $darkGray;
}
.business {
    max-width: em(900);
    margin: 0 auto em(105);
}
.section {
    margin-top: em(60);
    &__title, &__paragraph {
        @include breakpoint($xxs) {
            margin-right: 1.5rem;
            margin-left: 1.5rem;
        }
    }
    @include breakpoint($xxs) {}
    &__title {
        @include breakpoint($xxs) {}
    }
    &:not(.section_ladder) {}
}
.main {
    & h2, p {
        @include breakpoint($xs) {
            // padding-left: 1.5rem; 
            // padding-right: 1.5rem; 
        }
    }
    & p {
        font-size: em(21);
        @include breakpoint($xs) {
            font-size: em(18);
        }
    }
}
.ladder {
    margin: em(295) 0 em(205);
    flex-flow: column nowrap;
    display: flex;
    @include breakpoint($xxs) {
        margin: em(370) 0 em(140);
    }
}
.ladderStep {
    max-width: em(412);
    width: span(12);
    @include flyShadow();
    &_leftOffset {
        margin-left: span(1); //6.25%;
    }
    &_doubleLeftOffset {
        margin-left: span(2); //12.5%;
    }
    &_rightOffset {
        margin-right: span(1); //6.25%;
    }
    &_doubleRightOffset {
        margin-right: span(2); //12.5%;
    }
    &_left {
        align-self: flex-start;
    }
    &_right {
        align-self: flex-end;
    }
    &_center {
        align-self: center;
    }
    @include breakpoint($xxs) {
        margin: 0;
    }
    &:nth-child(2n+1) {
        @include breakpoint($xxs) {
            align-self: center;
        }
        ;
    }
    &:nth-child(4n+2) {
        @include breakpoint($xxs) {
            align-self: flex-end;
        }
    }
    &:nth-child(4n+4) {
        @include breakpoint($xxs) {
            align-self: flex-start;
        }
    }
    background-color: $red;
    font-family: $decorativeFont;
    text-align: center;
    padding: 1em .5em;
    text-transform: uppercase;
    color: $white;
    cursor: pointer;
    position: relative;
    &__description {
        min-width: span(12);
        top: 0 $i;
        @include breakpoint($xxs) {
            position: absolute;
            min-width: span(12) $i;
        }
    }
    transition: .3s ease;
    &:hover {
        background-color: $purple $i;
        z-index: 199;
    }
    &:nth-child(3n+1) {
        background-color: $purple;
        &:hover {
            background-color: $red $i;
            z-index: 199;
        }
    }
    &, &__refer {
        font-size: em(21);
    }
    &__refer {
        letter-spacing: .12em;
        background-image: none;
        font-weight: bold;
        &:hover {
            background-image: none;
        }
    }
    &__image {
        position: absolute;
        z-index: 0;
        &_diamond_president {
            bottom: 100%;
            @include breakpoint($xxs) {
                left: 0;
            }
        }
        &_diamond_executed_director, &_mr_manager {
            right: 100%;
        }
        &_mr_manager, &_diamond_executed_director, &_diamond_director {
            bottom: 1%;
            bottom: calc(0% + .2em);
        }
        &_registrationStep {
            @include breakpoint($xxs) {
                max-width: em(160 / 18 * 24, 16) $i;
                left: 0 $i;
            }
        }
    }
}
.ladderStepBenifit {
    &__title {
        font-size: 1em;
        margin: 1.5em 0 .5em;
        text-align: center;
    }
}
.benifitList {
    &Item {
        margin-top: .5em;
        &::before {
            content: "\2014\00a0\00a0";
        }
    }
}
.shares {
    max-width: em(920);
    margin: 0 auto 3em;
    &__title {
        // margin-bottom: em(50);
    }
}
.roomReferralRefer_refer, .share__title {
    position: relative;
    &:before {
        background: no-repeat center;
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background-size: cover;
    }
}
.share__title, .roomReferralRefer {
    &_refer {
        padding-bottom: .2em;
        text-indent: em(54, 24);
        color: $red;
        background-image: linear-gradient(to right, $red 0%, $red 100%);
        display: block;
    }
}
.roomReferralRefer {
    &_refer {
        max-width: em(210);
        margin-top: em(32);
        cursor: pointer;
        text-indent: em(35);
        &:before {
            background-image: url('./../img/icons/link.png');
            width: em(24);
            height: em(24);
        }
    }
    position: relative;
    &__notification {
        position: absolute;
        bottom: 40%;
        left: 0;
        z-index: 2;
        transition: .2s ease;
        opacity: 0;
        text-align: center;
        padding: .5em;
        max-width: em(210);
        margin: .5em 0;
        @include round(em(2));
        border: 1.5px solid $purple;
        background-color: $white;
    }
}
.share {
    // flex-direction: column;
    padding: 0 1.5rem;
    margin: 3rem auto;
    min-width: 33%;
    @include breakpoint($sm) {
        min-width: 50%;
    }
    @include breakpoint($xs) {
        min-width: 100%;
    }
    & p {
        padding: 0;
    }
    &__title {
        &:before {
            background-image: url('./../img/icons/gift.png');
            width: em(39.9, 24);
            height: em(32, 24);
            top: 3px;
            will-change: transform;
            transition: transform .5s ease-in;
        }
        &:hover {
            &.share__title:before {
                transform: scale(1.1);
            }
            & .share__title {
                &_refer {
                    $darkenRed: darken($red, 15%);
                    color: $darkenRed;
                    background-image: linear-gradient(to right, $darkenRed 0%, $darkenRed 100%);
                }
            }
        }
    }
    &__paragraph {}
    &__announce {
        display: block;
        display: -webkit-box;
        -webkit-line-clamp: 5; // margin-top: 1.5rem;
        // padding: 0 1em;
        overflow: hidden;
        height: em((18 * 5 * 1.5));
        text-overflow: ellipsis;
    }
}
.shareMeta {
    margin-top: .5em $i;
}
.room, .singleShare {
    max-width: em(950);
    margin: 0 auto em(235, 36);
}
.room {
    margin-bottom: 3em;
}
.singleShare {
    & p {
        margin-top: em(21);
    }
    & ul {
        margin-top: em(18);
    }
    & li:not(:first-child) {
        margin-top: em(15);
    }
    & h2 {
        margin-top: em(50, 24);
        &+p {
            margin-top: em(17);
        }
    }
    &__title {
        font-size: em(36);
        margin-bottom: em(16, 36);
    }
    @include breakpoint($sm-less) {
        padding: 0 1.5rem;
    }
}
.singleShareMeta {
    text-align: center;
    font-size: em(21) $i;
    margin-top: 0 $i;
}
.imageContainer_referral {
    background-color: $red;
    @include round(50%);
    margin-right: em(18.5);
}
.referralsList {
    margin-top: em(50);
    &__title {
        margin-bottom: em(32, 24);
    }
}
.referral {
    margin: 1.2em 0;
    padding: 0 1em 1.2em;
    &:not(:last-of-type) {
        border-bottom: 1px solid #949494;
    }
    &:hover {
        z-index: 1000;
    }
    &__title {
        font-family: $mainFont;
